@font-face {
  font-weight: 400;
  font-family: "Lato";
  src: local("Lato"), url("../../fonts/Lato-Regular.woff2") format("woff2"), url("../../fonts/Lato-Regular.woff") format("woff");
  font-display: swap;
}
@font-face {
  font-weight: 600;
  font-family: "Lato";
  src: local("Lato"), url("../../fonts/Lato-Semibold.woff2") format("woff2"), url("../../fonts/Lato-Semibold.woff") format("woff");
  font-display: swap;
}
@font-face {
  font-weight: 700;
  font-family: "Lato";
  src: local("Lato"), url("../../fonts/Lato-Bold.woff2") format("woff2"), url("../../fonts/Lato-Bold.woff") format("woff");
  font-display: swap;
}
@font-face {
  font-weight: 800;
  font-family: "Lato";
  src: local("Lato"), url("../../fonts/Lato-Heavy.woff2") format("woff2"), url("../../fonts/Lato-Heavy.woff") format("woff");
  font-display: swap;
}
html {
  height: 100%;
  scroll-behavior: smooth;
  overflow-x: hidden;
}

body {
  font-weight: 400;
  font-family: "Lato", sans-serif;
  display: flex;
  flex-flow: column;
  height: 100%;
}

.main {
  flex: 1 0 auto;
}

img {
  width: 100%;
}

* {
  box-sizing: border-box;
  margin: 0;
  outline: none;
}

h1,
h2,
h3,
h4,
h5,
h6 {
  margin: 0;
}

a {
  color: inherit;
  text-decoration: unset;
}

.overflow-h {
  overflow: hidden;
}

.container {
  width: 1208px;
  padding: 0 24px;
  margin: 0 auto;
  position: relative;
}

.container_flex_center {
  display: flex;
  align-items: center;
  justify-content: space-between;
}

.container_footer_grid {
  display: grid;
  grid-template-columns: 30% 1fr 30%;
  align-items: center;
  grid-template-areas: "copywriter payments feedback";
}

.logo {
  width: 247px;
  display: block;
}

.m-r-30 {
  margin-right: 30px;
}

.col-blue {
  color: #3E9CDC;
}

.title {
